import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

import style from './ContainerTools.module.css';

const MONTHS = [
  {
    value: 1,
    label: '',
  },
  {
    value: 2,
    label: '',
  },
  {
    value: 3,
    label: '',
  },
  {
    value: 4,
    label: '',
  },
  {
    value: 5,
    label: '',
  },
  {
    value: 6,
    label: '',
  },
  {
    value: 7,
    label: '',
  },
  {
    value: 8,
    label: '',
  },
  {
    value: 9,
    label: '',
  },
  {
    value: 10,
    label: '',
  },
  {
    value: 11,
    label: '',
  },
  {
    value: 12,
    label: '',
  },
];

function ContainerTools({ totalHours, totalWorkDay, priceByHour, onOpenModalPrice, changeMonth, currentMonth }) {
  const handleChange = (event) => {
    changeMonth(event.target.value);
    localStorage.setItem('month', JSON.stringify(event.target.value));
  };

  return (
    <li className={style.supportContainer}>
      <button className={style.btn} type="button" onClick={onOpenModalPrice}>
         /
      </button>
      <p>{`${totalHours} . * ${priceByHour} . = ${totalHours * priceByHour} . (${totalWorkDay} ${
        totalWorkDay > 4 ? '' : ''
      })`}</p>
      <TextField
        id="outlined-select-currency"
        select
        label=" "
        value={currentMonth}
        onChange={handleChange}
        helperText="  "
      >
        {MONTHS.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </li>
  );
}

export default ContainerTools;
